.menu {
  margin-top: 30px;
  text-align: center;
}
.menu ul {
    overflow: hidden;
}
.menu ul li {
      float: left;
      list-style: none;
      cursor: pointer;
      padding: 5px;
      margin: 5px;
      border: 1px solid #111;
      border-radius: 10px;
}
.menu ul li:focus {
      outline: none;
      color: red;
}
.menu .tireHeightLi {
    padding: 5px 10px;
}
.menu .tireHeight {
    clear: both;
}
.menu .inputSec {
    margin-top: 20px;
    padding: 20px 10px;
    clear: both;
}
.menu .inputSec span {
      font-size: 30px;
}
.menu .inputSec input {
      font-size: 30px;
      text-align: right;
      width: 50px;
      padding: 10px;
      border: 1px solid #111111;
      border-radius: 5px;
}
.menu #toggle, .menu #togglee {
    display: none;
    visibility: hidden;
}
.menu label {
    padding: 0.5em;
    text-align: center;
    color: #666;
}
.menu label:hover {
    color: #000;
}
.menu label::before {
    font-family: Consolas, monaco, monospace;
    font-weight: bold;
    font-size: 15px;
    content: "+";
    vertical-align: text-top;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 3px;
    background: radial-gradient(ellipse at center, #CCC 50%, transparent 50%);
}
.menu label ~ ul {
    height: 0px;
    overflow: hidden;
    -webkit-transition: height 0.5s;
    transition: height 0.5s;
}
.menu #toggle:checked ~ ul {
    height: 50px;
}
.menu #toggle:checked ~ label::before {
    content: "-";
}
.menu #togglee:checked ~ ul {
    height: 50px;
}
.menu #togglee:checked ~ label::before {
    content: "-";
}

.nav {
