        flex-wrap: wrap;
}
.nav ul.inch > li {
      width: 450px;
      list-style: none;
      text-align: center;
      font-size: 1.5rem;
      padding: 10px 20px;
      margin: 10px 20px;
      border: 1px solid #111111;
      border-radius: 10px;
}
.nav .navList ol {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    list-style-type: none;
    margin: 0px;
    padding: 0px;
}
.nav .navList ol li {
      font-size: 1rem;
      padding: 10px;
      border: none;
}
.nav .navList ol li .spec {
        width: 130px;
}
.nav .navList ol li .spec:hover {
        border-bottom: 1px solid blue;
}
.nav .navList .sp:hover > span {
    visibility: visible;
}
.nav .navList .ctlButton {
    visibility: hidden;
    margin-left: 20px;
}
.nav .navList .ctlButton .tireNum {
